import db from "libs/db";
import React from "react";
import { toast } from "react-toastify";
import AdminServices from "services/api/AdminServices";
import { initializeSmallCase, updateSmallCaseAuthId } from "services/smallcase";
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat";
import cssVar from "views/mobileView/styles";
import jwt_decode from "jwt-decode";
import { baseImgURL } from "assets/Constant";

export default function TopPannel() {
    const [user, setUser] = React.useState({})
    const [clientData, setClientData] = React.useState({});
    const [dematBalance, setDematBalance] = React.useState()
    const [loading, setLoading] = React.useState(false);

    React.useEffect(() => {
        db.get(db.model.user).then(async (user) => {
            setUser(user.userData)
            await getClientData(user.userData.id)
        })
        db.get(db.model.dematBalance).then(async (b) => {
            console.log(b)
            if (b) {
                setDematBalance(b)
            } else {
                setDematBalance(0)
            }
        })
    }, [])

    const getClientData = async (id) => {
        await db.get(db.model.clientData).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getClient(id).then(res => res);
                if (res.type === true) {
                    const a = JSON.parse(res.data);
                    let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                    console.log("Get Client API :", aa)
                    setClientData(aa)
                    await db.set(db.model.clientData, JSON.stringify(aa))
                }
            } else {
                setClientData(p)
            }
        })
    }

    const refreshDematBalance = async () => {
        var order = {
            "intent": "FETCH_FUNDS",
        }
        try {
            await initializeSmallCase();
            await db.get(db.model.smallcaseSDKToken).then(async (sdkToken) => {
                if (sdkToken === null || sdkToken === undefined || jwt_decode(sdkToken).smallcaseAuthId === undefined) {
                    console.log("user not connected")
                } else {
                    var res = await AdminServices.getSmallcaseTransactionId(order, jwt_decode(sdkToken).smallcaseAuthId).then(res => res)
                    console.log("trx id ",res.data.data.transactionId)
                    await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
                        .then(async (a) => {
                            console.log(a)
                            if (a.funds) {
                                try {
                                    console.log("blanace ",a.funds)
                                    setDematBalance(a.funds)
                                    await db.set(db.model.dematBalance, a.funds)
                                } catch (error) {
                                    toast.error(error)
                                }
                            }
                        })
                        .catch((p) => {
                            if(p.message === 'user_cancelled'){
                                return
                              } else {
                                toast.error('Something went wrong! Try again.')
                              }
                        });
                }
            })
        }
        catch (e) {
            console.log("chandak", e)
        }
    }

        return (
            <div className='menu-top-pannel'>
                <div style={{ width: "40px", height: "40px", backgroundColor: cssVar.getPropertyValue('--subtle'), color: cssVar.getPropertyValue('--darkGreen'), border: "1px solid " + cssVar.getPropertyValue('--light'), borderRadius: "20px", display: 'flex', justifyContent: "center", alignItems: 'center' }}>
                    <div className="bigHeader" style={{fontSize:'20px'}}>{clientData?.Client?.[0].DisplayName?.[0].toUpperCase() || ''}</div>
                </div>
                <div className='menu-top-pannel-cd'>
                    <div className='header1faint' style={{ textTransform: "capitalize" }}>{clientData?.Client?.[0].DisplayName}</div>
                    <div className='bodyTextLight'>Client ID: MB{clientData?.Client?.[0].ClientId}</div>
                </div>
                {
                    clientData?.SmallcaseInfo?.length > 0 ?
                        <div className='menu-top-pannel-demat'>
                            <div className='bodyTextLight'>Demat balance</div>
                            <div className='header1dark'>{CurrencyFormat(dematBalance,'long',2)} <img src={baseImgURL+"menu_refresh.webp"} height='18px' width='18px' onClick={() => { refreshDematBalance() }} />
                            </div>
                        </div>
                        :
                        <div className='menu-top-pannel-demat'>
                            <button className='solidButton' style={{whiteSpace:'nowrap',padding:'0 4%'}} onClick={async () => {
                                const res = await AdminServices.getSmallcaseTransactionId({ "intent": "CONNECT" }).then(rs => rs)
                                if (res.type) {
                                    console.log("Trx id =>>", res.data.data.transactionId)
                                    if (window.sc === undefined) {
                                        console.log("Initializing Smallcase")
                                        await initializeSmallCase();
                                    }
                                    await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
                                        .then((a) => {
                                            console.log(a)
                                            if (a) {
                                                updateSmallCaseAuthId(user.id, a.smallcaseAuthToken, a.broker).then(res=>{
                                                    console.log(res)
                                                    if(res){
                                                        db.remove(db.model.clientData)
                                                        toast.success('Demat linked')
                                                    } else {
                                                        toast.error('Demat already linked with another mintbox account')
                                                    }
                                                  })
                                            }
                                        })
                                        .catch((p) => {
                                            if(p.message === 'user_cancelled'){
                                                return
                                              } else {
                                                toast.error('Something went wrong! Try again.')
                                              }
                                        });
                                }
                            }}  >Link demat account</button>
                        </div>
                }
            </div>
        )
    }